html {
    width: 100vw;
    overflow-x: hidden;
    overflow-y: auto;
    overflow-y: hidden;
}

body {
    width: 100vw;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    background-color: black;
    overflow-x: hidden;
    overflow-y: hidden;
    transition: all 1s;
}

#root {
    width: 100vw;
    min-height: 100vh;
    margin: 0;
    padding: 0;
}

#app {
    width: 100vw;
    min-height: 100vh;
    margin: 0;
    padding-top: 1rem;
    color: white;
    font-family: 'Dosis', sans-serif;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: flex-start;
}

#title-section {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 1rem;
    padding-left: 1rem;
}

#title {
    margin: 0;
    font-size: 3rem;
}

#search-section {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    padding-left: 1rem;
}

#search-section > label {
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 1.5rem;
}

#search {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
}

#search > input {
    background-color: transparent;
    color: white;
    font-family: sans-serif;
    border-radius: 2px;
    border-width: 0px;
    border-bottom-width: 2px;
    border-style: solid;
    box-sizing: border-box;
    height: 2rem;
    padding: .25rem .5rem;
    margin-right: .25rem;
    margin-left: .25rem;
}

#search-input:focus {
    outline: none;
}

.search {
    height: 2rem;
    padding: .25rem .5rem;
    font-family: 'Dosis', sans-serif;
    border-style: solid;
    border-width: 2px;
    color: white;
    background-color: transparent;
}

select:focus {
    outline: none !important;
}

#new-search {
    margin-left: 1rem;
    margin-top: 1rem;
    border-color: inherit;
}
option {
    background-color: black;
}

#results-section {
    height: 75vh;
    width: 100vw;
    margin-right: 1rem;
    margin-bottom: 1rem;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    text-transform: capitalize;
}

#results-section > img {
    width: 20rem;
    height: auto;
}

#results {
    height: 100%;
    width: 100%;
    overflow: hidden;
    align-self: flex-start;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
}

#results > h2, h3, p {
    margin-top: 0;
    margin-bottom: 1.75rem;
}

#results > h2 {
    font-size: 3rem;
}

#results > h3 {
    font-size: 1.5rem;
}

@media screen and (min-width: 900px) {
    #title-section {
        justify-content: center;
        padding: 0;
    }

    #app, #search-section {
        align-items: center;
        padding: 0;
    }

    #app {
        padding-top: 1rem;
    }

    #results-section, #new-search {
        margin: 0;
    }

    #new-search {
        margin-top: 1rem;
    }
}